@charset "UTF-8";

// BI, 후원하기  z-index:500
#header {
	.logo { position:fixed; top:30px; left:30px; z-index:500; }
	.donate_link {
		width:200px; height:65px; position:fixed; top:30px; right:60px; border:2px solid #d6251d; font-size:20px; font-weight:$light; background:#d6251d; color:$white;
		text-align:center; line-height:65px; z-index:500; border-radius:10px; transition:0.3s;
		&:hover {
			color:#d6251d; background-color:$white;
		}
	}
}
#footer {
	min-width:1420px; height:184px; background-color: #1c202c; overflow:hidden;
	.footer_wrap {
		width: 1180px; margin: 0 auto;
		.footer_top {
			padding: 27px 0 70px; line-height: 42px;
			.left_box {
				float: left;
				.f_logo { float: left; }
				ul {
					float: left; font-size: 14px; margin: 0 0 0 25px;
					li {
						float: left; margin: 0 0 0 35px;
						a {
							color: #fff; position: relative; display: block;
							&:after { width: 0; height: 2px; display: block; content: ""; position: absolute; bottom: 0; left: 0; background-color: #e60012; -webkit-transition: width 0.3s ease; -moz-transition: width 0.3s ease; -o-transition: width 0.3s ease; -ms-transition: width 0.3s ease; }
							&:hover:after { width: 100%; }
						}
					}
				}
			}
			.right_box {
				float: right; font-size: 14px;
				dl {
					float: left; position: relative; padding: 0 0 0 36px;
					&:first-child:after { width: 0; }
					&:after { width: 1px; height: 11px; display: block; content: ""; position: absolute; top: 15px; left: 18px; background-color: #434650; }
					dt { color: #ffe043; float: left; }
					dd { float: left; color: #fff; margin: 0 0 0 14px; }
				}
			}
		}
		.footer_bottom {
			padding: 25px 0; border-top: 1px solid #333742; color: #fff; line-height: 44px;
			.address { float: left; color: #7e7f81; font-size: 13px; }
			a {
				width: 150px; height: 44px; color: #fff; background-color: #e60012; font-size: 14px; float: right; text-align: center; -webkit-border-radius: 44px; -moz-border-radius: 44px; border-radius: 44px; -webkit-transition: background-color 0.2s ease; -moz-transition: background-color 0.2s ease; -o-transition: background-color 0.2s ease; -ms-transition: background-color 0.2s ease;
				&:hover { background-color: #ea4c42; cursor: pointer; }
			}
		}
	}
}















































