// BI, 후원하기  z-index:500
#container {
    min-width:1420px; background-color:#fbf5eb;
    #nav {
        opacity:0; position:fixed; left:200px; top:50%; transform:translateY(-50%); z-index:500;
        .page {
            .section_link { display:block; text-align:center; font-size:14px; font-weight:$bold; font-style:italic; }
            &:after { width:23px; height:1px; display:block; margin:1px 0 0; background-color:#a1977c; content:''; }
        }
        .active_bright {
            &:after { width:23px; height:6px; display:block; position:absolute; z-index:-1; margin:-4px 0 0; background-color:#f8cac7; content:''; transform:skew(-12deg, 0); }
        }
        .active_bright_text {
            color:#d14840;
        }
        .active_dark {
            &:after { width:23px; height:6px; display:block; position:absolute; z-index:-1; margin:-4px 0 0; background-color:#d14840; content:''; transform:skew(-12deg, 0); }
        }
        .active_dark_text {
            color:$white;
        }
        .page + .page { margin:30px 0 0; }
    }
    #section_01 {
        height:970px; position:relative; overflow:hidden;
        background:url("../../images/common/section_01/section_01_bg.jpg") no-repeat;
        .main_title_bg { position:absolute; top:45%; left:50%; transform:translate(-50%, -50%); opacity:0; }
        .main_title {
            width:1100px; height:700px; position:relative; top:50%; left:50%; transform:translate(-50%, -50%);
            img { position:absolute; }
            .main_title_01 { top:150px; left:200px; opacity:0; }
            .main_title_02 { top:250px; left:300px; opacity:0; }
            .main_title_03 { top:350px; left:800px; opacity:0; }
        }
        .main_title_bg { width:1700px; position:absolute; top:50%; left:50%; transform:translate(-50%, -50%); }
        .scroll_down { position:absolute; left:50%; transform:translateX(-50%); animation:scrollDownAni 2s infinite; }
        @-webkit-keyframes scrollDownAni {
            0% {
                opacity:0;
                bottom:80px;
            }
            20% {
                opacity:1;
            }
            50% {
                opacity:1;
            }
            65% {
                opacity:0.2;
            }
            100% {
                opacity:0;
                bottom:50px;
            }
        }
    }
